<template>
    
    <div id="withdrawal">
    	<c-title :hide="false" text='收入提现'></c-title>
    	<div style="height: 40px;"></div>
   <el-table :data="datasource" border  stripe style="width: 100%" @select="handleSelectionChange" @select-all="selectAll" align='center'>
    <el-table-column type="selection" width="45" :selectable="isCheck">
    </el-table-column>
    <el-table-column  label="类型" width="105">
      <template scope="scope">{{ scope.row.type_name }}</template>
    </el-table-column>
    <el-table-column prop="income" label="金额" show-overflow-tooltip>
    </el-table-column>
    <el-table-column prop="poundage" label="手续费" show-overflow-tooltip>
    </el-table-column>
  </el-table>   	
    	
			<div id="userinfo">
			<ul>
				<li><span>{{totalwithdrawal}}</span><br>提现金额合计</li>
				<li><span>{{poundage}}</span><br>手续费合计</li>
			</ul>
			</div>

  <span class="wrapper">
    <el-button type="success" @click="withdrawToBalance('balance')">提现到帐户余额</el-button>
    <el-button type="danger" @click="withdrawToBalance('wechat')">提现到微信余额</el-button>
    <el-button type="info" :plain="true" @click="toRecord">提现记录</el-button>
  </span>

    </div>
   
</template>
<script>
import member_income_withdrawal_controller from './member_income_withdrawal_controller';
export default member_income_withdrawal_controller;

</script>
<style lang="scss" rel="stylesheet/scss" scoped>
#withdrawal{
    #userinfo{ background: #FFF; margin: 10px 0;
    ul{ display: flex;align-items:center;}
    ul li:last-child{border: 0;}
    ul li {color: #8c8c8c;text-align: center;display: block; flex: 1;}
    ul li  span{color: #222;font-size: .9rem}
    ul li{ float: left;width: 33.3333%;margin:10px 0;border-right: 1px solid #e3e3e3;box-sizing: border-box;}
    }
    .wrapper{ 
    	display: flex;padding: 0 10px;flex-flow: row wrap;margin-top: 50px;
    	button{flex: 100%;    margin: 5px auto;}
    }

}
</style>


