import cTitle from 'components/title';
import { MessageBox } from 'mint-ui';
export default {
  data() {
    return {
      datasource: [],
      //提现总金额
      totalwithdrawal: '',
      //手续费
      poundage: '',
      //存储数据的全选数据
      multipleSelection: [],
      //存储数据的单选数据
      selection: [],
      iscommission: false,
      isdividend: false,
      netdata: ''
    }
  },
  methods:
  {
    //仅对 type=selection 的列有效，类型为 Function，Function 的返回值用来决定这一行的 CheckBox 是否可以勾选
    isCheck(row, index) {

      return this.datasource[index].selected;
    },
    //单选
    handleSelectionChange(val, row) {
      console.log(val);
      if (row.can) {
        //this.multipleSelection.push(val);
        //this.multipleSelection.push(row.type_name);
        console.log(val.key_name);
        //设置当前选择项
        if (val.key_name == 'commission') {
          this.iscommission = true;

        } else {
          this.isdividend = true;

        }
        row.can = false;
        this.totalwithdrawal = this.FloatAdd(row.income, this.totalwithdrawal);//parseFloat(row.amount)+parseFloat(this.totalwithdrawal);
        this.poundage = this.FloatAdd(row.poundage, this.poundage);//parseFloat(row.poundage) + parseFloat(this.poundage);

      } else {
        //设置当前选择项
        if (val.key_name == 'commission') {
          this.iscommission = false

        } else {
          this.isdividend = false;

        }
        row.can = true;
        this.multipleSelection.splice(this.multipleSelection.indexOf(val), 1);
        this.totalwithdrawal = this.FloatSub(this.totalwithdrawal, row.income);//parseFloat(this.totalwithdrawal) - parseFloat(row.amount) ;
        this.poundage = this.FloatSub(this.poundage, row.poundage);//parseFloat(this.poundage) - parseFloat(row.poundage);



      }
      console.log(this.multipleSelection);
      // //四舍五入保留两位小数
      // this.totalwithdrawal = String(parseFloat(this.totalwithdrawal).toFixed(2));
      // this.poundage = String(parseFloat(this.poundage).toFixed(2));//this.poundage.toFixed(2);
    },
    //浮点数加法运算
    FloatAdd(arg1, arg2) {
      var r1, r2, m;
      try { r1 = arg1.split(".")[1].length } catch (e) { r1 = 0 }
      try { r2 = arg2.split(".")[1].length } catch (e) { r2 = 0 }
      m = Math.pow(10, Math.max(r1, r2));
      return (arg1 * m + arg2 * m) / m;
    },

    //浮点数减法运算
    FloatSub(arg1, arg2) {
      var r1, r2, m, n;
      try { r1 = arg1.split(".")[1].length } catch (e) { r1 = 0 }
      try { r2 = arg2.split(".")[1].length } catch (e) { r2 = 0 }
      m = Math.pow(10, Math.max(r1, r2));
      //动态控制精度长度
      n = (r1 = r2) ? r1 : r2;
      return ((arg1 * m - arg2 * m) / m).toFixed(n);
    },
    //全选
    selectAll(val) {
      //计算费用和总提现金额
      //console.log(val);
      this.multipleSelection = val;
      this.totalwithdrawal = 0;
      this.poundage = 0;
      if (val.length > 0) {
        //计算费用和总提现金额
        for (let i = 0; i < val.length; i++) {
          this.datasource[i].can = false;
          this.totalwithdrawal += parseFloat(this.datasource[i].income);
          this.poundage += parseFloat(this.datasource[i].poundage);
          //设置当前选择项
          if (this.datasource[i].key_name == 'commission') {
            this.iscommission = true;

          } else if (this.datasource[i].key_name == 'dividend') {
            this.isdividend = true;

          }
        }
        //四舍五入保留两位小数
        // this.totalwithdrawal = this.totalwithdrawal.toFixed(2);
        // this.poundage = this.poundage.toFixed(2);
      } else {
        for (let i = 0; i < this.datasource.length; i++) {
          if (this.datasource[i].selected) {
            this.datasource[i].can = true;
            //设置当前选择项
            if (this.datasource[i].key_name == 'commission') {
              this.iscommission = true;

            } else if (this.datasource[i].key_name == 'dividend') {
              this.isdividend = true;

            }
          }


        }

        this.totalwithdrawal = 0;
        this.poundage = 0;

      }

      console.log(this.multipleSelection);
    },
    //发起http请求
    getNetData() {
      var that = this;
      $http.get('finance.income.get-withdraw', { member_id: 9 }, '正在获取列表...').then(function (response) {
        //console.log(response.data);
        if (response.result == 1) {
          var mydata = response.data;
          that.netdata = mydata;
          if(mydata.commission)
          {
            that.datasource.push(mydata.commission);

          }
          if(mydata.dividend)
          {
            that.datasource.push(mydata.dividend);

          }
          
          console.log(that.datasource);
          // that.iscommission  = mydata.commission.can;
          // that.isdividend = mydata.isdividend.can;

        }
      }, function (response) {
        // error callback
      });

    },
    toRecord()
    {
      this.$router.push('/member/presentationRecord');

    }
    
    ,
    withdrawToBalance(type) {
      //finance.income.save-withdraw
      console.log(type);
      var data = { total: { amounts: this.totalwithdrawal, poundage: this.poundage, pay_way: type }};
      var commission;
      var dividend;
      if (this.multipleSelection.length > 0) {

        for (let i = 0; i < this.multipleSelection.length; i++) {
          var item = this.multipleSelection[i];
          console.log(item.key_name);
          if (item.key_name == 'commission') {
            
            commission = item
             data = { total: { amounts: this.totalwithdrawal, poundage: this.poundage, pay_way: type },commission:commission};
            
          } else if (item.key_name == 'dividend') {
            //dividend ={ dividend: item };
            if(commission)
            {
              data = { total: { amounts: this.totalwithdrawal, poundage: this.poundage, pay_way: type },commission:commission,dividend:item};

            }else
            {
              odata = { total: { amounts: this.totalwithdrawal, poundage: this.poundage, pay_way: type },dividend:item};
            }

          }
    

        }
        
      
        var that = this;
        $http.post('finance.income.save-withdraw', { data: data }, '正在提交数据...').then(function (response) {
          console.log(response.data);
          if (response.result == 1) {
             MessageBox('提示', response.msg);
             that.$router.go(-1);

          }
        }, function (response) {
          // error callback
        });

      } else {
        MessageBox('提示', '提现数据不可为空');

      }


    }

  },
  activated() {
    this.datasource = [];
    this.getNetData();


  },
  components: { cTitle }
}


